import store from './../../store'
const onClick = (e) => {
  console.log('clicked')
}

const chapterContextMenu = [
  {
    type: 'ctx-handle',
    name: 'delete chapter',
    onClick: () =>{
      var state = store.getState()
      var removeEl = Object.assign({}, state.note.ctxTargetEl)
      store.dispatch({type: 'REMOVE_CHAPTER'})

      // remove from the server
      console.log('Removing from the server')
      store.dispatch({type: '@SERVER_REMOVE_CHAPTER', el: removeEl})
    }
  },
  {
    type: 'ctx-handle',
    name: ['make disabled', 'make enabled'],
    nameIndex: 0,
    onClick: () =>{
      store.dispatch({type: 'TOGGLE_DISABLED'})
    }
  },
  {
    type: 'separator'
  },
  {
    type: 'ctx-handle',
    name: ['add icon', 'remove icon'],
    nameIndex: 0,
    onClick: onClick
  }
]

const topicContextMenu = [
  {
    type: 'ctx-handle',
    name: 'delete topic',
    onClick: (el) =>{
      var state = store.getState()
      var removeEl = Object.assign({}, state.note.ctxTargetEl)

      store.dispatch({type: 'REMOVE_TOPIC'})

      // remove from the server
      console.log('Removing from the server')
      store.dispatch({type: '@SERVER_REMOVE_TOPIC', el: removeEl})
    }
  },
  {
    type: 'ctx-handle',
    name: ['make disabled', 'make enabled'],
    nameIndex: 0,
    onClick: (el) =>{
      store.dispatch({type: 'TOGGLE_DISABLED'})
    }
  },
  {
    type: 'separator'
  },
  {
    type: 'ctx-handle',
    name: ['add icon', 'remove icon'],
    nameIndex: 0,
    onClick: onClick
  }
]

export {
  chapterContextMenu,
  topicContextMenu
}


